body {
    font-family: "Arial", sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 20px;
  }
  
  .App {
    max-width: 400px;
    margin: auto;
    background: white;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
  }
  
  h1 {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
  }
  
  ul {
    list-style-type: none; /* Changed to none for cleaner look */
    padding: 0;
  }
  
  .Content {
    display: flex;
    justify-content: space-between; /* Space between list item and delete button */
    align-items: center; /* Center items vertically */
    margin: 10px 0; /* Margin for spacing between items */
    background: #e7f3fe;
    padding: 10px;
    border-radius: 4px;
  }
  
  li {
    flex: 1;
    background: #e7f3fe;
    padding: 10px;
    border-radius: 4px;
    transition: background 0.3s;
    overflow-wrap: break-word;
    cursor: pointer;
  }
  .delete-button {
    margin-left: 15px;
    color: #ff0000; /* Red color for delete button */
    cursor: pointer;
    transition: color 0.3s;
  }
  
  .delete-button:hover {
    color: #cc0000; /* Darker red on hover */
  }
  
  input {
    width: calc(100% - 22px);
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 10px;
    font-size: 16px;
  }
  
  button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    transition: background 0.3s;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  .Done {
    text-decoration: line-through;
    color: #000; 
  }
  
  .Empty {
    text-decoration: none;
  }
  